@import url(https://fonts.googleapis.com/css?family=Roboto);
*,
::after,
::before {
  box-sizing: border-box;
}

body {
  height: 100%;

  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.5em;

  color: rgba(0, 0, 0, 0.87);
  background: #f5f5f5;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App-container {
  position: fixed;
  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);
}

.App-clocks-container {
  display: flex;
  flex-wrap: wrap;
}

.Clock-name {
  margin-top: 0;
  margin-bottom: 0;

  text-align: center;

  overflow-wrap: anywhere;
}

.Clock {
  width: 100%;
  height: 150px;
}

.Clock-box {
  position: relative;

  width: 120px;
}

.Clock-face__circle {
  fill: none;
  stroke: #000000;
  stroke-width: 9;
}

.Clock-face__hour-marks {
  fill: none;
  stroke: #000000;
  stroke-width: 9;
}

.Clock-face__mid-circle {
  fill: #0c0909;
}

.Clock-hour__hour-arm {
  fill: none;
  stroke: #000000;
  stroke-width: 17;
}

.Clock-minute__minute-arm {
  fill: none;
  stroke: #000000;
  stroke-width: 11;
}

.Clock-second__second-arm {
  fill: none;
  stroke: #000000;
  stroke-width: 4;
}

.Clock-sizing-box {
  fill: none;
}

.Clock-hour,
.Clock-minute,
.Clock-second {
  transform-origin: 300px 300px;
}

.Clock__delete {
  position: absolute;
  top: 30px;
  right: 0;

  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 30px;
  height: 30px;

  font-weight: 700;
  font-size: 15px;

  color: #000000;
  border: 1px solid #000000;
  border-radius: 50%;
  text-decoration: none;

  -webkit-user-select: none;

      -ms-user-select: none;

          user-select: none;
}

.Form {
  display: inline-flex;
  align-items: flex-end;
  margin-bottom: 30px;
}

.Form-control {
  display: flex;
  flex-direction: column;
  margin-right: 20px;
}

.Form-control input {
  width: 150px;
  margin-top: 10px;
}

.Form input,
.Form button {
  border: 1px solid #000000;
  border-radius: 5px;
}

.Form-control__name {
  padding: 10px;
}

.Form-control__user-timezone,
.Form-control__button-add {
  padding: 10px;
}

.Form-control__button-add {
  background-color: #ffffff;
}

